<template>
    <div class="logo">
        <img src="@/assets/img/school_logo.png" style="width: 200px;" title="logo">
        <div class="headtext">中国近代那些事</div>
    </div>
    <hr class="hr-sep-page"/>
    <br/>
    <div class="members">
        <h3 @click="flodInit()" style="margin-left: 2em;">小组成员</h3>
        <div v-for="way, index in contacts" :key="index">
            <h4 class="contact" @click="flod(way)" style="font-weight: 800;">{{ way.name }} : {{ way.id }}</h4>
            <Transition name="slide-fade">
                <div v-show="way.show">
                    <blockquote class="content" style="font-weight: 500;">{{ way.content }}</blockquote>
                </div>
            </Transition>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
export default {
    name: 'SideBar',
    setup() {
        function flod(way) {
            if (way.show){
                flodInit()
            }
            else{
                for (let i = 0; i < contacts.value.length; i++){
                    contacts.value[i].show = false
                }
                way.show = true
            }
        }
        function flodInit() {
            for (let i = 0; i < contacts.value.length; i++){
                contacts.value[i].show = false
            }
            contacts.value[0].show = true
        }

        let contacts = ref([
            {
                name: '魏程浩',
                id: '162210118',
                content: "SomeThing",
                show: true,
            },
            {
                name: '黄佩全',
                id: '162210125',
                content: "通过学习中国近代史的大事件，我对中国历史有了更加全面的认知，并且深刻体会到历史对于我们的重要性。我希望能够以这些历史为镜，着眼于未来，为建设一个更加繁荣、和谐的中国贡献自己的力量。",
                show: false,
            },
            {
                name: '池明轩',
                id: '162210219',
                content: "SomeThing",
                show: false,
            },
            {
                name: '徐子新',
                id: '162210116',
                content: "SomeThing",
                show: false,
            },
            {
                name: '赵璟烨',
                id: '162250208',
                content: "通过学习中国近代史的大事件，我对中国历史有了更加全面的认知，并且深刻体会到历史对于我们的重要性。我希望能够以这些历史为镜，着眼于未来，为建设一个更加繁荣、和谐的中国贡献自己的力量。",
                show: false,
            },
        ])

        return {
            flod,
            flodInit,

            contacts,
        }
    }
}
</script>

<style scoped>
@font-face {
    font-family: kai;
    src: url('@/fonts/SimKai.ttf');
}

.logo {
    display: block;
    width: 90%;
    margin-top: 10%;
    text-align: center;
}

.members {
    margin-top: 10%;
}

.hr-sep-page {
    margin-left: auto;
    margin-right: auto;
    width: 70%;
    height: 1px;
    border-top: 1px dashed black;
}

